<template>
	<div :class="rootClass" id="root">
		<router-view></router-view>
	</div>
</template>

<script>
import { environment } from '@configPath/globalConfigs/production.config';
import storageCache from '@/utils/localstorage-cache'
export default {
  name: 'app',
  data: function() {
    return {
      rootClass: storageCache.memoryCache.get("rootMobileClass")
    }
  	
  },
  beforeCreate: function () {
  	if("production" !== environment || "https:" === window.location.protocol) {
		this.$root.isMobile = !1
		this.$root.environment = environment

  	} else {
  		const location = window.location.href.subtring(window.location.protocol.length);
  		window.location.href = "https:" + location;
  	}
  },
  watch: {
  	$route: function() {
  		this.$el.scrollTop = 0
  	}
  }
}
</script>



