
let temp = {}

export default {
	setLocalStorageCache(key, value) {
		const localStorage = window.localStorage;
		const str = JSON.stringify({
			timestamp: (new Date).getTime(),
			value
		})
		localStorage.setItem(key, str)
	},
	getLocalStorageCache(key) {
		const item = window.localStorage.getItem(key)
		if(undefined === item || null === item) return null;
		return JSON.parse(item).value
	},
	removeLocalStorageCache(key) {
		window.localStorage.removeItem(key)
	},
	getLocalStorageInfo() {
		const item = window.localStorage.getItem(key)
		if(undefined === item) return;
		return JSON.parse(item)
	},
	memoryCache: {
		set: function(key, value) {
			temp[key] = {
                timestamp: Date.now(),
                value
            }
		},
		get: function(key) {
			return temp[key] ? temp[key].value : null
		},
		remove: function(key) {
			undefined !== temp[key] && delete temp[key]
		},
		getInfo: function(t) {
			return temp[t]
		}
	}
}


