export default function (t, e, n) {

	if ("number" != typeof t) return t;

    switch (e) {
	    case "split":
	        return function (t) {
	            var e = [],
	                n = t,
	                i = "";
	            for (; n > 1e3;) e.push(n % 1e3), n = Math.floor(n / 1e3);
	            e.push(n);
	            for (; e.length > 0;) i += e.pop() + ",";
	            return i.substr(0, i.length - 1)
	        }(t);
	    case "byte":
	        return function (t) {
	            var e = 0;
	            if (t >= 1e3 && t < 1e6) return {
	                value: e = parseFloat(t / 1024),
	                unit: "KB",
	                text: e + "KB"
	            };
	            if (t >= 1e6 && t < 1e9) return {
	                value: e = parseFloat(t / 1048576),
	                unit: "MB",
	                text: e + "MB"
	            };
	            if (t >= 1e9) return {
	                value: e = parseFloat(t / 1073741824),
	                unit: "GB",
	                text: e + "GB"
	            };
	            return {
	                value: t,
	                unit: "B",
	                text: t + "B"
	            }
	        }(t);
	    case "percent":
	        return function (t, e) {
	            var n = Math.round(100 * t);
	            "fill" === e && n < 10 && (n = "0" + n);
	            return n + "%"
	        }(t, n);
	    case "time":
	        return function (t) {
	            var e = t / 1e3 / 60 / 60,
	                n = Math.floor(e),
	                i = 60 * (e - n),
	                a = Math.floor(i),
	                o = 60 * (i - a),
	                r = Math.floor(o);
	            return {
	                hour: n,
	                minute: a,
	                second: r,
	                millisecond: 1e3 * (o - r)
	            }
	        }(t);
	    default:
	        return t >= 1e3 && t < 1e6 ? parseFloat((t / 1e3).toFixed(n || 2)) + "K" : t >= 1e6 || t <= -1e6 ? parseFloat((t / 1e6).toFixed(n || 2)) + "M" : t
	    }
}


