<template>
	<form @submit="(e)=> {onSubmit(e)}">
		<slot />
	</form>
</template>

<script>
export default {
	name: "mhy-form",
	methods: {
		onSubmit: function(e) {
			e && e.preventDefault()
			let flag = false;
			this.$children.forEach((c) => {
				const children = c;
				if(children.constructor.options.name.indexOf('mhy-form-') >= 0){
					children.onBlur();
					const $input = children.$el.querySelector('.input-container input')
					$input && $input.blur()
					children.isError && (flag = true, $input && $input.foucs)
				}
			})
			flag || this.$emit("submit", e)
		}
	}
}
</script>

<style>
	
</style>


