<template>
	<mhy-form ref="form" @submit="onSubmit">
		<mhy-form-input 
			class="input-item"
            :placeholder="$t('mobile')"
            required
            v-model="mobile"
            inputValidate="mobile"
		></mhy-form-input>
		<mhy-form-input 
			class="input-item"
            :placeholder="$t('captcha')"
            captcha
            :mobile="mobile"
            v-model="mobile_captcha"
            :actionType="action_type"
		></mhy-form-input>
		<mhy-button class="login-btn" block type="submit">{{$t("login.signUp")}}</mhy-button>

	</mhy-form>
</template>

<script>
import {actionType} from '@configPath/actionConfigs/customAction.config'
export default {
	name: "captcha-login",
	data: function() {
		return {
            mobile: "",
	        mobile_captcha: "",
	        action_type: actionType.LOGIN
		}
	},
	methods: {
		onSubmit: function () {
			_.isEmpty(this.mobile_captcha) 
			? this.$toast({
				content: this.$t("captchaRequire")
			})
			: this.$emit("submit", {
                mobile: this.mobile,
                mobile_captcha: this.mobile_captcha
			})
		}
	}
}
</script>

<style>
	
</style>


