import MhyTabs from '@/components/MhyTabs/MhyTabs'   //引入组件
import MhyFormInput from '@/components/MhyFormInput/MhyFormInput'   //引入组件
import MhyButton from '@/components/MhyButton/MhyButton'   //引入组件
import MhyForm from '@/components/MhyForm/MhyForm'   //引入组件
import MhyToast from '@/components/MhyToast/MhyToast'   //引入组件
import MhyIcon from '@/components/MhyIcon/MhyIcon'   //引入组件


export const mhyComponent = {
  install (Vue, options) {
    Vue.component(MhyTabs.name, MhyTabs) //全局组件
    Vue.component(MhyFormInput.name, MhyFormInput) //全局组件
    Vue.component(MhyButton.name, MhyButton) //全局组件
    Vue.component(MhyForm.name, MhyForm) //全局组件
    Vue.component(MhyIcon.name, MhyIcon) //全局组件
  }
};


export const toastPlugin = {
    install(Vue, options){
        Vue.prototype.$toast = function(options = {}) {
			const instance = new (Vue.extend(MhyToast));
			Object.assign(instance, options);
			instance.$mount();
            const parentNode = document.querySelector(".mhy-toast");
            parentNode && parentNode.parentElement.removeChild(parentNode);
            document.body.appendChild(instance.$el);
            //return instance;
    	}
    }
};


