import {get, post} from '@/utils/http';

export default {
  /**
   * 获取用户信息
   * @param options
   * @returns {*}
   */
  login_by_password: function(options = {}) {
    return post('/Api/login_by_password', options);
  },
  login_by_mobilecaptcha: function(options = {}) {
    return post('/Api/login_by_mobilecaptcha', options);
  },
  logout: function(options = {}) {
    return get('/Api/logout', options);
  },
  login_by_cookie: function(options = {}) {
    return get('/Api/login_by_password', options);
  },
  create_mobile_captcha: function(options = {}) {
    return post('/Api/create_mobile_captcha', options);
  },
}



