import Vue from 'vue'
import VueI18n from 'vue-i18n'

Vue.use(VueI18n)

const messages = {
    'zh-cn': {
      login: {
        captachLogin: '验证码登录',
        passwordLogin: '密码登录'
      },
      home: {
        accountManagement: '账号管理',
        gameInfoManagement: '游戏信息与管理'
      },
      mhyFormInput: {
        captchaText: "获取验证码",
        isSend: "重新发送"
      },
    },
    en: {
      login: {
        captachLogin: '验证码登录！',
        passwordLogin: '密码登录'
      }
    }
}

export default new VueI18n({
    locale: "zh-cn", // 语言标识
    messages
})



