import Vue from 'vue'
import Router from 'vue-router'
import VerifyContainer from '@/main/VerifyContainer/VerifyContainer'
import Login from '@/main/Login/Login'
import CaptchaLogin from '@/main/Login/CaptchaLogin'
import PasswodLogin from '@/main/Login/PasswordLogin'
// import Register from '@/main/Register/Register'
// import EmailRegister from '@/main/Register/EmailRegister'
// import MobileRegister from '@/main/Register/MobileRegister'
// import RegisterSuccess from '@/main/Register/RegisterSuccess'

import {beforeRouterUpdate, afterRouterUpdate} from '@/lib/router/index'

const Home =  () => import('@/main/AccountCenter/Home')
const MainPage =  () => import('@/main/AccountCenter/MainPage/MainPage')
const Game =  () => import('@/main/AccountCenter/Game/Game')



Vue.use(Router)


const routes = [{
    path: "/",
    redirect: "/account"
}, {
    path: "/account",
    component: Home,
    redirect: "/account/home",
    children: [{
        path: "home",
        component: MainPage,
        meta: {
            title: "账号管理"
        }
    }, {
        path: "game",
        component: Game,
        meta: {
            title: "账号管理"
        }
    }]
}, {
    path: "/verify",
    component: VerifyContainer,
    redirect: "/login",
    children: [{
        path: "/login",
        component: Login,
        redirect: "/login/captcha",
        children: [{
            path: "captcha",
            component: CaptchaLogin,
            meta: {
                title: "验证码登录"
            }
        }, {
            path: "password",
            component: PasswodLogin,
            meta: {
                title: "密码登录"
            }
        }]
    }]
    
}];


const router = new Router({
	routes: routes
})


router.beforeEach((to, from, next) =>{
    beforeRouterUpdate.bind(Vue)(to, from, next)
})
router.afterEach( (to, from, next) =>{
    afterRouterUpdate.bind(Vue)(to, from, next)
})

export default router




