<template>
	<div class="mhy-toast">
		{{content}}
	</div>
</template>
<script>
export default{
	name:"mhy-toast",
	props:{
		duration: {
			type: Number,
			default: 3e3
		},
		content: {
			type: String,
			default: ""
		}
	},
	mounted: function() {
		setTimeout(() => {
			this.$el.parentElement && this.$el.parentElement.removeChild(this.$el)
			this.$destroy()
		}, this.duration)
	}
}
</script>


