<template>
	<div class="mhy-button" :class="block ? 'is-block': ''">
		<a v-if="'a' === buttonType" :class="getClasses" @click="onClick">
			<slot />
		</a>
		<button v-else :class="getClasses" @click="onClick">
			<slot />
		</button>
	</div>
</template>

<script>
export default {
	name: "mhy-button",
	props: {
		block: {
			type: Boolean,
			default: false
		},
		buttonType: {
			type: String,
			default: "button"
		},
		to: {
			type: String
		},
		ghost: {
			type: Boolean,
			default: false
		}
	},
	inheritAttrs: false,
	methods: {
		getClasses: function (){
			let calssName = ""
			return calssName +=this.ghost ? " is-ghost" : ""
		},
		onClick: function() {
			this.to && this.$router.push(this.to)
		}
	}

}
</script>

<style>
	
</style>


