<template>
	<div 
		class="mhy-tabs"
		:class="flex ? 'tabs-flex' : ''"
	>
		<div 
			v-for="(e, t) in tabList" 
			:key="t"
			:ref="`tabItem${t}`"
			class="tab-item" 
			:class="active === t ? 'active' : ''"
			@click="() => {
				selectTab(t)
			}"
		>
			{{e.text}}
		</div>
		<i 
			class="tab-active-line"
			:class="complete ? 'transition' : ''"
			:style="{
				left: lineleft,
				width: lineWidth
			}"
		></i>
	</div>
</template>

<script>
import numberFormat from '@/utils/numberFormat'
export default {
	name: "mhy-tabs",
	props: {
		tabList: {
			type: Array,
			required: true
		},
		flex: {
            type: Boolean,
            default: true
        },
        activeIndex: {
            type: Number
        }
	},
	data: function() {
		return {
			tabIndex: 0,
			lineWidth: 0,
			lineleft: "",
			complete: false
		}
	},
	computed: {
		active: {
			get: function () {
				return undefined !== this.activeIndex ? this.activeIndex : this.tabIndex
			}
		}
	},
	mounted: function () {
		this.setWidthLeft()
		setTimeout(() => this.complete = true)
	},
	methods: {
		setWidthLeft: function (t) {
			const index = undefined === t ? this.active : t;
			if(this.flex) {
				let n = 1 / this.tabList.length; 
				this.lineWidth = numberFormat(n, "percent")
				this.lineleft = numberFormat(n * index, "percent")
			} else {
				const dom = this.$ref[`tabItem${index}`][0]
				this.lineWidth = dom.clientWidth + 'px',
				this.lineleft = dom.offsetLeft + 'px'

			}
		},
		selectTab: function(t) {
			this.tabIndex = t
			this.$emit("update:activeIndex", t)
			this.$emit("change",this.tabList[t], t)

		}
	}, 
	watch: {
		active: function (t) {
			this.setWidthLeft(t)
		}
	}
}
</script>

<style>
	
</style>


