<template>
	<div class="mhy-login mhy-container-content">
		<img class="mihoyo-logo" :src="mihoyoLogo" @click="()=>{$router.push('/')}"/>
		<div class="tab-container">
			<mhy-tabs
				class="login-tab"
				:tabList="tabList"
				:activeIndex="'/login/captcha' === $route.path ? 0 : 1"
				@change="onTabChange"
			></mhy-tabs>

		</div>
		<router-view @submit="onLogin"></router-view>
		<!--<mhy-footer></mhy-footer>-->

	</div>
</template>

<script>
import storageCache from '@/utils/localstorage-cache'
import api from '../../models/api'
export default {
	name: "login",
	data: function() {
		return {
			mihoyoLogo: require('../../resoures/images/miHoYo_Logo.png'),
			tabList: [{
				text: this.$t("login.captachLogin"),
				value: 'captcha'
			}, {
				text: this.$t("login.passwordLogin"),
				value: "password"
			}]
		}
	},
	beforeMount: function() {
		storageCache.removeLocalStorageCache('login_ticket'),
		storageCache.removeLocalStorageCache('actionInfo'),
		storageCache.removeLocalStorageCache('accountInfo')
	},
	methods: {
		onTabChange: function(t) {
			this.$router.push(`/login/${t.value}`)
		},
		onLogin: function(t) {
			const by_mobilecaptcha = "/login/captcha" === this.$route.path
			const data = t;



			by_mobilecaptcha 
			? api.login_by_mobilecaptcha(data).then(({status, account_info, msg}) => {
				if(1 === status) {
					const {weblogin_token} = account_info;
					storageCache.setLocalStorageCache("login_ticket", weblogin_token)
					storageCache.setLocalStorageCache("accountInfo", account_info)
					this.$router.push("/")
					/*this.$bindAccount("channel", ()=> {
						this.$router.push("/")
					})*/
				}else{
					this.$toast({content:msg})
				}
			})
			: api.login_by_password(data).then(({status, account_info, msg}) => {
				if(1 === status) {
					const {weblogin_token} = account_info;
					storageCache.setLocalStorageCache("login_ticket", weblogin_token)
					storageCache.setLocalStorageCache("accountInfo", account_info)
					this.$router.push("/")
					/*this.$bindAccount("channel", ()=> {
						this.$router.push("/")
					})*/
				}else{
					this.$toast({content:msg})
				}
			})
			/*this.$http.post({
				url: by_mobilecaptcha ? "/login_by_mobilecaptcha" : "login_by_password",
				data,
				onSuccess: ({status, acction_info, msg})=> {
					if(1 === status) {
						const {weblogin_token} = acction_info;
						storageCache.setLocalStorageCache("login_ticket", weblogin_token)
						storageCache.setLocalStorageCache("accountInfo", acction_info)
						this.$bindAccount("channel", ()=> {
							this.$router.push("/")
						})
					} else{

					}
				}
			})*/
		}
	}

}
</script>

<style>
	
</style>


