<template>
	<mhy-form ref="form" @submit="onSubmit">
		<mhy-form-input 
			class="input-item"
            :placeholder="$t('login.account')"
            required
            v-model="account"
		></mhy-form-input>
		<mhy-form-input 
			class="input-item"
			type="password"
            :placeholder="$t('password')"
            :inputValidate="inputValidate($t('mhyFormInput.passwordRequire'))"
            required
            v-model="password"
		></mhy-form-input>
		<mhy-button class="login-btn" block type="submit">{{$t("login.signUp")}}</mhy-button>

	</mhy-form>
</template>

<script>
export default {
	name: "password-login",
	data: function() {
		return {
            account: "",
	        password: "",
	        mmt_key: ""
		}
	},
	methods: {
		inputValidate: (errorText) => (val) =>{return "" === val ? errorText : null},
		onSubmit: function () {
			this.$emit("submit", Object.assign({
				account: this.account,
				//password: this.$RSAencrypt.encrypt(this.password),
				password: this.password,
				is_crypto: true
			}, {}))
		}
	}
}
</script>

<style>
	
</style>


