import storageCache from '@/utils/localstorage-cache'

export function beforeRouterUpdate(to, from, next){
	const LOGIN_TICKET = storageCache.getLocalStorageCache("login_ticket");
    const ACTIONINFO = storageCache.getLocalStorageCache("actionInfo");
	console.log(to.query)
	console.log(from.query)
	to.path.indexOf("/account/") >= 0 && !LOGIN_TICKET 
	? next("/login")
	: to.path.indexOf("verify") >=0 && !ACTIONINFO 
		? next("/login") 
		: 0 === to.matched.length ? "/" !== from.path && from.path ? next(!1) : next("/") : next()
	//0 === to.matched.length ? to.path ? next(!1) : next("/") : next()
}
export function afterRouterUpdate(to, from, next){

}


