<template>
	<div class="mhy-form-input" :class="isError ? 'input-error': ''">
		<div class="input-container" :class="isFocus ? 'focus' : ''">
			<input 
				ref="input"
				:type="type"
				:vaule="value"
				@input="onInput"
				@focus="onFocus"
				@blur="onBlur"
			>
			<div 
				v-if="captcha"
				class="input-inner-btn"
				:class="isSend ? 'disabled': ''"
				click-upload=""
				@click="sendCaptcha"
			>
				{{captchaText}}
			</div>
			<div class="input-label" :class="'' === value ? '' : 'label-at-top'">
				{{placeholder}}
			</div>
		</div>
		<p v-if="isError" class="error-text">
			<mhy-icon name="closecircle"></mhy-icon>
			{{errorText}}
		</p>
		<p v-if="!isError && pwdStrength" class="password-strength">
			<span class="strength-title">{{$t('mhyFormInput.passwordStrength')}}</span>
			<span class="strength-bar" :class="passwordStrength"></span>
			<span class="strength-text" :class="passwordStrength">{{$t(`mhyFormInput.${passwordStrength}`)}}</span>
		</p>
	</div>
</template>

<script>
import validator from 'validator';
import storageCache from '@/utils/localstorage-cache'
import api from '../../models/api'
let timer = null
export default {
	name: "mhy-form-input",
	props: {
		placeholder: {
			type: String,
			default: ""
		},
		type: {
			type: String,
			default: "text"
		},
		required: {
			type: Boolean,
			default: false
		},
		inputValidate: {
			type: [String, Function],
			validator: (t) => t instanceof Function || -1 !== ["mobile", "email", "password"].indexOf(t)
		},
		value: {
			type: String,
			default: ""
		},
		mobile: {
			type: String,
			default: ""
		},
		validate: {
			type: Function
		},
		captcha: {
			type: Boolean,
			default: !1
		},
		action_type: {
			type: String
		}
	},
	inheritAttrs: false,
	data: function() {
		return {
			isFocus: false,
			blured: false,
			countDown: 60,
			isSend: false,
			pwdStrength: "",
			captchaText: this.$t("mhyFormInput.captchaText"),
			errorText: null
		}
	},
	computed: {
		isError: {
			get: function() {
				return !(!this.errorText || !this.blured)
			}
		}
	},
	methods: {
		onInput: function() {
			"email" !== this.inputValidate && "mobile" !== this.inputValidate || (this.$refs.input.value = this.$refs.input.value.trim());
			const val = this.$refs.input.value;
			this.$emit('input', val)

			this.errorText = this.validateInput(val)
		},
		onFocus: function() {
			this.isFocus = true
			this.$emit("focus", this.$event)
		},
		onBlur: function() {
			const val = this.$refs.input.value
			this.isFocus = false
			this.blured = true
			this.errorText = this.validateInput(val);
			this.$emit("blur", this.$event)
			window.scrollTo && window.scrollTo(0, 0)
		},
		validateInput: function(t) {
			let errorText = null;
			// 验证必填 但值为空时
			if(!this.required &&  "" === t) return null
			// 自定义验证方法
			if(this.inputValidate instanceof Function) errorText = this.inputValidate(t)
			// 内部定义【mobile,email,password】的验证方法
			else switch(this.inputValidate) {
				case "mobile":
					"" === t 
					? errorText = this.$t("mhyFormInput.mobileRequired")
					: validator.isMobilePhone(t, "zh-CN") 
					|| (errorText = this.$t("mhyFormInput.mobileError"));
					break;
				case "email":
					"" === t 
					? errorText = this.$t("mhyFormInput.emialRequire") 
					: validator.isEmail(t) 
					|| (errorText = this.$t("mhyFormInput.emailError"));
				case "password":
					"" === t 
					? errorText = this.$t("mhyFormInput.passwordRequire") 
					: /^[a-zA-Z0-9`~i!@#$%^&*()\-_=+[\]{}\\|'";:,.<>/? ]{6,15}$/.test(t) 
					|| (errorText =  t.length > 15 || t.length < 6 ? this.$t("mhyFormInput.pwdLengthError") : this.$t("mhyFormInput.pwdError"));
					let pwdStrength = 0;
					[/[0-9]/, /[a-z]/, /[A-Z]/, /[`~i!@#$%^&*()\-_=+[\]{}\\|'";:,.<>/? ]/, /.{8,}/].forEach((regexp) => {
                        regexp.test(t) && (pwdStrength += 1)
                    })
                    switch (pwdStrength) {
                    	case 1:
                    	case 2:
                    	    this.pwdStrength = "week";
                    		break;
                    	case 3:
                    	case 4:
                    		this.pwdStrength = "normal";
                    		break;
                    	case 5:
                    		this.pwdStrength = "strong";
                    		break;
                    	default:
                    		this.pwdStrength = "";
                    }
                    break;
                default:
                	errorText = "" === t ? this.$t("mhyFormInput.required") : null

			}
			return errorText

		},
		sendCaptcha: function() {
			if(!this.isSend) {
				if(validator.isMobilePhone(this.mobile, "zh-CN")) {
					const ACTION_INFO = storageCache.getLocalStorageCache("actionInfo")
					api.create_mobile_captcha({
						action_type: this.action_type,
						mobile: this.mobile,
						action_ticket: _.get(ACTION_INFO, "action_ticket")
					}).then(({status, msg}) => {
						if(1 === status ){
							this.isSend = true
							this.captchaText = this.$t("mhyFormInput.isSend")
							timer = setInterval(() => {
								if(this.countDown <= 0 ) {
									this.isSend = false
									this.captchaText = this.$t("mhyFormInput.captchaText")
									this.countDown = 60
									return clearInterval(timer)
								}
								this.countDown -= 1
								this.captchaText = `${this.$t("mhyFormInput.isSend")} (${this.countDown}s)`
							}, 1e3)
						} else{
							this.$toast({
								content: msg
							})
						}
					})
					/*this.$http.post({
						url: '/create_mobile_captcha',
						data: {
							action_type: this.action_type,
							mobile: this.mobile,
							action_ticket: _.get(ACTION_INFO, "action_ticket")
						},
						onSuccess: ({status, msg}) => {
							if(1 === status ){
								this.isSend = true
								this.captchaText = this.$t("mhyFormInput.isSend")
								timer = setInterval(() => {
									if(this.countDown < 0 ) {
										this.isSend = false
										this.captchaText = $t("mhyFormInput.captchaText")
										this.countDown = 60
										return clearInterval(timer)
									}
									this.countDown -= 1
									this.captchaText = `${$t("mhyFormInput.isSend")} (${this.countDown}s)`
								}, 1e3)
							} else{

							}
							
						}
					})*/
				} else this.$toast({
	                content: this.$t("mhyFormInput.sendCaptchaMsg")
	            })
			}
		}
	},
	beforeDestroy: function() {
		clearInterval(timer)
	}
}
</script>

<style>
	
</style>


